<template>
  <div id="app">
    <!-- 这里是主页面(入口页面),而Header和Footer组件直接被用在了这个,说明每个页面都有这两个组件 -->
    <!-- 只是Footer组件设置了position固定住了,而Footer组件没有固定,而且被放在了路由出口下面,所以Footer组件一直在最下面 -->
    <Header/>
    <MainLayout>
      <keep-alive>
        <router-view />
      </keep-alive>
    </MainLayout>
    <Footer />
  </div>
</template>

<script>
import Header from '@/components/common/Header.vue'
import Footer from '@/components/common/Footer.vue'
import MainLayout from '@/components/layout/MainLayout.vue'
import { mapMutations } from 'vuex'

export default {
  name: 'App',
  data () {
    return {
    }
  },
  components: {
    Header,
    Footer,
    MainLayout
  },
  methods: {
    ...mapMutations('test', ['setBaseURL'])
  },
  // 使用钩子函数来设置vuex里面的数据
  created () {
    const BaseURL = localStorage.getItem('BaseURL')
    this.setBaseURL(BaseURL)
  }
}
</script>

<style scoped>
#app {
  font-family: Arial, sans-serif;
  color: #333;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
}
</style>
