import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './utils/element-ui'
// import './utils/lodash'

Vue.config.productionTip = false
Vue.config.devtools = true

// 全局注册自定义指令
Vue.directive('focus', {
  inserted (element) {
    // console.log(element)
    element.focus()
  }
})

new Vue({
  router,
  store,
  // Vue 实例的选项中并没有 lodash 这个字段，所以这种方式是不正确的。正确的做法是将 lodash 挂载到 Vue 的原型上，或者在组件中按需引入。
  // lodash,
  render: h => h(App)
}).$mount('#app')
