import Vue from 'vue'
import VueRouter from 'vue-router'
/* import Home from '../views/Home.vue'
import About from '../views/About.vue'
import NotFind from '../views/NotFind.vue'
import Store from '../views/Store.vue'
import Test from '@/views/Test.vue'
import Login from '@/views/Login.vue'
import Register from '@/views/Register.vue'
import Test02 from '@/views/Test02.vue' */

// 路由懒加载
const Home = () => import(/* webpackChunkName: "group-01" */ '../views/Home.vue')
const About = () => import(/* webpackChunkName: "group-01" */ '../views/About.vue')
const Store = () => import(/* webpackChunkName: "group-02" */ '../views/Store.vue')
const NotFind = () => import(/* webpackChunkName: "group-03" */ '../views/NotFind.vue')
const Test = () => import(/* webpackChunkName: "group-04" */ '@/views/Test.vue')
const Login = () => import(/* webpackChunkName: "group-05" */'@/views/Login.vue')
const Register = () => import(/* webpackChunkName: "group-05" */'@/views/Register.vue')
const Test02 = () => import(/* webpackChunkName: "group-07" */'@/views/Test02.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/store',
    name: 'Store',
    component: Store
  },
  {
    path: '/test',
    name: 'Test',
    component: Test
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/test02',
    name: 'test02',
    component: Test02
  },
  {
    path: '*',
    component: NotFind
  }
]

const router = new VueRouter({
  mode: 'hash',
  // base: process.env.BASE_URL,
  routes,
  // 组件守护器
  beforeRouteEnter (to, from, next) {
    // A跳转到B，B页面停留在A页面的滚动位置；解决方法：将scrollTop设置为0
    window.scroll(0, 0)
    next()
  }/* ,
  beforeEach (to, from, next) {
    // A跳转到B，B页面停留在A页面的滚动位置；解决方法：将scrollTop设置为0
    window.scroll(0, 0)
    next()
  } */
})

// 路由的前置守卫
router.beforeEach((to, from, next) => {
  // 将滚动位置设置为顶部
  window.scrollTo(0, 0)
  next()
})

export default router
