<template>
  <div class="main-layout">
    <!-- 这两个命名插槽没有被使用 -->
    <slot name="header"></slot>
    <main class="main-content">
      <!-- 下面这个是默认插槽，about、home等路由组件的出口就在这，默认插槽被包在两个div里面，这两个div设置了width等限制盒子的大小 -->
      <slot></slot>
    </main>
    <slot name="footer"></slot>
  </div>
</template>

<script>
export default {
  name: 'MainLayout'
}
</script>

<style scoped>
.main-layout {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
  padding: 20px;
  /* 这里限制了路由出口的宽度，因为默认插槽被包在两个div里面 */
  max-width: 1200px;
  margin: 0 auto;
}
</style>
